import React from 'react';
import logo from './logo.svg';
import './App.css';
import './weui.min.css';

const log = str => {
  var d = new Date()
  console.log(d.toLocaleTimeString() + ' ' + d.getMilliseconds() + ":" + str)
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      token: "",      // 授信凭证
      vtDeviceId: "", // 虚拟设备编号
    };
    log('react constructor');
  }

  componentDidMount() {
    log('react componentDidMount');

    /**
     * 初始化桥接程序
     */
    /* global fastman */
    log('fastman ready start');
    fastman.ready(_=>{
      log('fastman ready end');
      const _this = this;

      log('fastman oauth start');
      // 所有的桥接接口都必须在ready接口执行成功后才可被调用，如果需要在页面加载时就调用相关桥接接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
      fastman.oauth({
        success: function (res) {
          log('fastman oauth success');
          // _this.setState({...res});
          log('token:' + res.token);
          log('vtDeviceId:' + res.vtDeviceId);
          log('fastman oauth end');
        },
        fail: function (err) {
          log('fastman oauth fail');
          log('fastman oauth end');
        }
      });
    });
  }

  componentWillUnmount() {

  }

  oauth() {
    const _this = this;
    fastman.oauth({
      success: function (res) {
        log('fastman oauth success');
        _this.setState({...res});
        log('token:' + res.token);
        log('vtDeviceId:' + res.vtDeviceId);
        log('fastman oauth end');
      },
      fail: function (err) {
        log('fastman oauth fail');
        log('fastman oauth end');
      }
    });
  }

  render() {
    return (
      <div class="container" id="container">
        <div class="page input js_show">
            <div class="page__bd">
                <div style={{textAlign:"center"}}><img src={logo} className="App-logo" alt="logo" style={{height:"10vmin"}} /></div>
                <div class="weui-cells__title">react注册桥接并授权</div>
                <div class="weui-cells weui-cells_form">
                    <div class="weui-cell">
                        <div class="weui-cell__hd"><label class="weui-label">token</label></div>
                        <div class="weui-cell__bd">
                            <input id="token" class="weui-input" type="text" value={this.state.token} />
                        </div>
                    </div>
                    <div class="weui-cell">
                        <div class="weui-cell__hd"><label class="weui-label">vtDeviceId</label></div>
                        <div class="weui-cell__bd">
                            <input id="vtDeviceId" class="weui-input" type="text" value={this.state.vtDeviceId} />
                        </div>
                    </div>
                </div>
                <div class="weui-btn-area">
                    <a id="btn1" class="weui-btn weui-btn_default" href="javascript:" onClick={this.oauth}>授权</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">登录</div>
                <div class="weui-btn-area">
                    <a id="btn2" class="weui-btn weui-btn_default" href="javascript:">唤起登录</a>
                </div>
                <div class="weui-cells weui-cells_checkbox">
                  <label class="weui-cell weui-check__label" for="s11">
                    <div class="weui-cell__hd">
                      <input type="checkbox" class="weui-check" name="checkbox1" id="s11" checked="checked" />
                      <i class="weui-icon-checked"></i>
                    </div>
                    <div class="weui-cell__bd">
                      <p>普通资金账号</p>
                    </div>
                  </label>
                  <label class="weui-cell weui-check__label" for="s12">
                    <div class="weui-cell__hd">
                      <input type="checkbox" name="checkbox1" class="weui-check" id="s12" />
                      <i class="weui-icon-checked"></i>
                    </div>
                    <div class="weui-cell__bd">
                      <p>两融账号</p>
                    </div>
                  </label>
                  <label class="weui-cell weui-check__label" for="s13">
                    <div class="weui-cell__hd">
                      <input type="checkbox" name="checkbox1" class="weui-check" id="s13" />
                      <i class="weui-icon-checked"></i>
                    </div>
                    <div class="weui-cell__bd">
                      <p>期权账号</p>
                    </div>
                  </label>
                </div>
                <div class="weui-btn-area">
                    <a id="btn14" class="weui-btn weui-btn_default" href="javascript:">加强版登录</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">分享</div>
                <div class="weui-btn-area">
                    <a id="btn3" class="weui-btn weui-btn_default" href="javascript:">微信朋友圈分享</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn4" class="weui-btn weui-btn_default" href="javascript:">微信好友分享</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn5" class="weui-btn weui-btn_default" href="javascript:">QQ好友分享</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn6" class="weui-btn weui-btn_default" href="javascript:">微博分享</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">导航</div>
                <div class="weui-cell">
                    <div class="weui-cell__hd"><label class="weui-label">H5 URL</label></div>
                    <div class="weui-cell__bd">
                        <input id="url" class="weui-input" type="text" placeholder="请输入H5 URL" value="https://m.taobao.com/#index" />
                    </div>
                    <div id="btnClearText" class="weui-cell__ft">
                        <i class="weui-icon-clear"></i>
                    </div>
                </div>
                <div class="weui-btn-area">
                    <a id="btn7" class="weui-btn weui-btn_default" href="javascript:">前进</a>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd"><label class="weui-label">原生 URL</label></div>
                    <div class="weui-cell__bd">
                        <input id="url2" class="weui-input" type="text" placeholder="请输入原生 URL" value="dfyj://dl/fundproductlist" />
                    </div>
                    <div id="btnClearText2" class="weui-cell__ft">
                        <i class="weui-icon-clear"></i>
                    </div>
                </div>
                <div class="weui-btn-area">
                    <a id="btn11" class="weui-btn weui-btn_default" href="javascript:">前进</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn8" class="weui-btn weui-btn_default" href="javascript:">后退</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">第三方调用</div>
                <div class="weui-btn-area">
                    <a id="btn9" class="weui-btn weui-btn_default" href="javascript:">模拟成功调用</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn10" class="weui-btn weui-btn_default" href="javascript:">模拟失败调用</a>
                </div>
            </div>

            <div class="page__bd">
                <div class="weui-cells__title">设置导航头</div>
                <div class="weui-btn-area">
                    <a id="btn12" class="weui-btn weui-btn_default" href="javascript:">设置</a>
                </div>
            </div>

            <div class="page__bd">
                <div class="weui-cells__title">微信支付</div>
                <div class="weui-btn-area">
                    <a id="btn13" class="weui-btn weui-btn_default" href="javascript:">测试支付</a>
                </div>
            </div>

        </div>
    </div>
    );
  }
}

export default App;
